.annotation {
    position:absolute;
    z-index:99999;
    transition: all 0.125s;
    animation: 0.375s ease-in-out annotation forwards;
    animation-delay: 0.375s;
    opacity:0;
}

.annotation.annotation--success .annotation__content {
    border: 5px solid green;
}

.annotation__highlight {
    position:absolute;
    transition: all 0.125s;
    border: 5px solid red;
    border-radius: 5px;
    pointer-events: none;
}

.annotation__line {
    position:absolute;
    background:red;
    pointer-events: none;
}

.annotation--success .annotation__highlight,
.annotation--success .annotation__line {
    display:none;
}

.annotation__content {
    position: absolute;
    border: 5px solid red;
    border-radius: 5px;
    background:#ffffff;
    padding:0.75rem;
}

@keyframes annotation {
    from {
        opacity:0;
    }
    to {
        opacity: 1;
    }
}