.targetindicator {
    box-sizing: border-box;
    position:absolute;
}
.targetindicator__content {
    height:100%;
    width:100%;
    animation-delay: 1s;
    background: repeating-linear-gradient(
            -45deg,
            rgba(255,255,255,0.75),
            rgba(255,255,255,0.75) 5px,
            rgba(255,255,255,0) 5px,
            rgba(255,255,255,0) 10px
    );
}

.targetindicator--animate-both .targetindicator__content {
    width:0;
    height:0;
    animation: 1s ease-in-out targetindicator--width forwards, 1s ease-in-out targetindicator--height forwards;
}

.targetindicator--animate-width .targetindicator__content {
    width:0;
    animation: 1s ease-in-out targetindicator--width forwards;
}
.targetindicator--animate-height .targetindicator__content {
    height:0;
    animation: 1s ease-in-out targetindicator--height forwards;
}

@keyframes targetindicator--width {
    from {
        width:0;
    }
    to {
        width:100%;
    }
}

@keyframes targetindicator--height {
    from {
        height:0;
    }
    to {
        height:100%;
    }
}